import React from 'react'
import './login.css'

export default function login() {

    const Year = new Date().getFullYear();
    return (
        <>
            <section>
                <div class="container h-custom">
                    <div class="row d-flex justify-content-center align-items-center h-100">
                        <div class="col-md-9 col-lg-6 col-xl-5" style={{ display: "flex", alignItems: "center", justifyContent: "center", margin: "50px" }} >
                            <img src="/assets/Habesha.png" className="img-fluid" alt="Sampleimage" style={{ height: "300px" }} />
                        </div>
                        <div class="col-sm-8 col-lg-6 col-xl-4 offset-xl-1 mt-2 gradient-custom-2">
                            <form style={{ marginTop: "50px" }} className='p-5'>
                                <div class="d-flex flex-row align-items-center justify-content-center justify-content-lg-start">
                                    <p class="lead fw-normal mb-0 me-3">Sign in with</p>
                                    <button type="button" class="btn btn-dark btn-floating mx-1">
                                        <i class="fa fa-facebook" aria-hidden="true"></i>
                                    </button>

                                    <button type="button" class="btn btn-dark btn-floating mx-1">
                                        <i class="fa fa-twitter" aria-hidden="true"></i>
                                    </button>

                                    <button type="button" class="btn btn-dark btn-floating mx-1">
                                        <i class="fa fa-linkedin" aria-hidden="true"></i>
                                    </button>
                                    <button type="button" class="btn btn-dark btn-floating mx-1">
                                        <i class="fa fa-google" aria-hidden="true"></i>
                                    </button>
                                </div>

                                <div class="divider d-flex align-items-center my-4">
                                    <p class="text-center fw-bold mx-3 mb-0">Or</p>
                                </div>


                                <div class="form-outline mb-4">
                                    <input type="email" id="form3Example3" class="form-control form-control-lg"
                                        placeholder="Enter a valid email address" />
                                    <label class="form-label" for="form3Example3">Email address</label>
                                </div>


                                <div class="form-outline mb-3">
                                    <input type="password" id="form3Example4" class="form-control form-control-lg"
                                        placeholder="Enter password" />
                                    <label class="form-label" for="form3Example4">Password</label>
                                </div>

                                <div class="d-flex justify-content-between align-items-center">

                                    <div class="form-check mb-0">
                                        <input class="form-check-input me-2" type="checkbox" value="" id="form2Example3" />
                                        <label class="form-check-label" for="form2Example3">
                                            Remember me
                                        </label>
                                    </div>
                                    <a href="#!" class="text-body">Forgot password?</a>
                                </div>

                                <div class="text-center text-lg-start mt-4 pt-2">
                                    <button type="button" class="btn btn-dark btn-lg"
                                        style={{
                                            paddingLeft: "2.5rem",
                                            paddingRight: "2.5rem",
                                            fontFamily: "Roboto"
                                        }}>
                                        Login
                                    </button>
                                    <p class="small fw-bold mt-2 pt-1 mb-0">Don't have an account? <a href="/register" class="link-danger">Register</a></p>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        </>
    )
}
